import PageLayout from '../components/PageLayout';
import Utils from '../util/Utils';

import '../styles/grid.scss';

class Grid extends React.Component {
    state = {
        highlight: null,
        names: Utils.getNames(),
    };
    componentDidMount() {
        Utils.loadVoices();
        window.setInterval(() => {
            const highlight = this.state.names.length
            this.setState({ highlight });
        }, 500);
    }
    render() {
        const { names } = this.state;
        const colCount = Math.ceil(Math.sqrt(names.length));
        const rowCount = Math.ceil(names.length / rowCount);

        const style = {
            width: 100 / colCount + '%',
            height: 100 / rowCount + '%'
        };

        return (
            <PageLayout className="grid">
                <div className="page_content">
                    {names.map(n =>
                        <div className="name-wrapper" key={n.value} style={style}>
                            <div className="name">{n.value}</div>
                        </div>
                    )}
                </div>
            </PageLayout>
        );
    }
}

export default Grid;